export const config = {
  openai: {
    apiKey: import.meta.env.VITE_OPENAI_API_KEY,
  },
  anthropic: {
    apiKey: import.meta.env.VITE_ANTHROPIC_API_KEY,
  },
  emailjs: {
    serviceId: import.meta.env.VITE_EMAILJS_SERVICE_ID,
    templateId: import.meta.env.VITE_EMAILJS_TEMPLATE_ID,
    publicKey: import.meta.env.VITE_EMAILJS_PUBLIC_KEY,
  },
};

export function validateEnvVariables() {
  const missingVars = [];
  
  if (!import.meta.env.VITE_OPENAI_API_KEY) {
    missingVars.push('OpenAI API Key');
  }
  if (!import.meta.env.VITE_EMAILJS_SERVICE_ID) {
    missingVars.push('EmailJS Service ID');
  }
  if (!import.meta.env.VITE_EMAILJS_TEMPLATE_ID) {
    missingVars.push('EmailJS Template ID');
  }
  if (!import.meta.env.VITE_EMAILJS_PUBLIC_KEY) {
    missingVars.push('EmailJS Public Key');
  }

  if (missingVars.length > 0) {
    throw new Error(
      `Missing required environment variables:\n${missingVars.join('\n')}\n\n` +
      'Please add these to your .env file.'
    );
  }
}

validateEnvVariables();